import {Stopwatch, randInt} from './custom/utils'
import {Keyboard_Key} from './keyboard.key.js'
import {keyHeight} from './cste.js'


export class Keyboard {
	constructor(session) {
		this.session = session
		this.music = this.session.music
		this.controls = new KeyboardControls(this)
		
		this.stopwatch = new Stopwatch()
		this.stopwatch.speed = this.music.speed
		this.cycles = 0
		
		this.enabled = false
		this.previewEnabledState = false
		this.keyList = []
		this.missedKeys = new Set()
		this.init()
	}

	init() {
		this.initKeys()
		this.chooseKeyLocations()
	}

	initKeys() {
		this.keyList = this.music.data.keyList.map((noteList,i)=>{
			return new Keyboard_Key(this, noteList)
		})
		this.keyList[0].isStarter = true
	}

	chooseKeyLocations() {
		this.keyList[0].location = randInt(0,3)
		for (let i = 1; i < this.keyList.length; i++) {
			const L1 = this.keyList[i-1].location
			this.keyList[i].location = (L1+randInt(1,3))%4
		}
	}

	getCurrentKeys() {
		const N_max = Math.ceil(window.innerHeight/keyHeight)+2
		const now = this.stopwatch.time
		const t_min = Math.floor(now)
		const t_max = t_min+N_max
		const currentKeys = []
		for (let t = t_min; t < t_max; t++) {
			const id = this.music.timeKeysMap[t]
			const k = this.keyList[id]
			if(id===undefined)continue;
			currentKeys.push(k)
		}
		return currentKeys
	}

	isFinished() {
		const now = this.stopwatch.time
		return now>=0 && this.music.timeKeysMap[Math.floor(now)]===undefined
	}

	checkMissedKey() {
		const currentKeys = this.getCurrentKeys()
		const k0 = currentKeys[0]
		if(!this.missedKeys.has(k0)&&k0&&k0.isPassed()&&!k0.state.taken) {
			this.missedKeys.add(k0)
			return true
		}
		return false
	}

	refresh() {
		if(this.checkMissedKey()) {
			this.fail()
		}
		if(this.isFinished()) {
			this.restart()
		}
	}

	fail() {
		if(!this.enabled)return;
		this.session.fail()
	}

	restart() {
		this.missedKeys.clear()
		this.cycles++
		this.keyList.forEach(k=>k.resetState())
		this.chooseKeyLocations()
		this.stopwatch.setTime(-10)
	}

	pause() {
		this.previewEnabledState = this.enabled
		this.stop()
	}

	resume() {
		if(this.previewEnabledState) {
			this.start()
		}
	}

	start() {
		this.stopwatch.start()
		this.enabled = true
	}

	stop() {
		this.stopwatch.stop()
		this.enabled = false
	}

	draw(g) {
		this.refresh()
		this.getCurrentKeys().forEach(k=>k.draw(g))
	}
}




class KeyboardControls {
	constructor(keyboard) {
		this.keyboard = keyboard
		this.canvas = this.keyboard.session.canvas
		this.enabled = false
		this.canvas.ontouchstart = e=>this.mouseDown(e)
		this.canvas.ontouchend = e=>this.mouseUp(e)
	}

	mouseDown(e) {
		const [x, y] = [e.changedTouches[0].pageX, e.changedTouches[0].pageY]

		const k = this.keyboard.getCurrentKeys().find(k=>k.isInRectangle(x,y))
		if(k) {
			k.mouseDown(x,y)
		} else {
			this.keyboard.fail()
		}
	}

	mouseUp(e) {
		const [x, y] = [e.changedTouches[0].pageX, e.changedTouches[0].pageY]
		const k = this.keyboard.getCurrentKeys().find(k=>k.isInRectangle(x,y))
		if(k) {
			k.mouseUp()
		}
	}
}