import {keyHeight} from './cste'

export class Keyboard_Key {
	constructor(keyboard, data, isStarter) {
		this.keyboard = keyboard
		this.data = data
		this.location = 0
		this.isStarter = isStarter||false
		this.playingNotes = []
		this.state = {}
		this.resetState()
		this.stopwatch = this.keyboard.stopwatch
	}

	resetState() {
		this.state = {
			selected_t: -1,
			selected_swtime: -1,
			finished_swtime: -1,
			taken: false,
			finished: false
		}
		this.playingNotes.forEach(obj=>obj.cancel())
		this.playingNotes = []
	}

	calculateRectangle() {
		const time = this.stopwatch.time
		const t0 = this.data.t0
		const d = Math.floor(this.data.t2-this.data.t0)
		const x_min = this.location*(window.innerWidth/4)
		const y_min = window.innerHeight-(t0-time+d)*keyHeight
		const x_max = x_min + window.innerWidth/4
		const y_max = y_min + d*keyHeight
		const width = window.innerWidth/4
		const height = d*keyHeight
		return {x_min, y_min, x_max, y_max, width, height}
	}
	
	refresh() {
		if(this.isFinished()) {
			this.finish()
		}
	}


	isFinished() {
		const e = this.stopwatch.time-this.state.selected_swtime>this.data.t2-this.state.selected_t
		return this.state.taken&&(this.state.finished||e)
	}
	finish() {
		if(this.state.finished)return;
		this.state.finished = true
		this.state.finished_swtime = this.stopwatch.time
		this.reportProgression()
	}
	getProgression() {
		return this.state.finished?this.state.finished_swtime-this.state.selected_swtime:0
	}
	reportProgression() {
		this.keyboard.session.addScore(Math.ceil(this.getProgression()))
	}
	

	isPassed() {
		return this.stopwatch.time>this.data.t2
	}

	isInRectangle(x, y, r) {
		const {x_min, y_min, x_max, y_max, height} = r||this.calculateRectangle()
		return x_min<x && x<x_max && y_min<y+0.2*height && y-0.2*height<y_max
	}

	draw(g) {
		this.refresh()
		const r = this.calculateRectangle()
		g.fillStyle = this.state.taken?"green":"black"
		g.fillRect(r.x_min, r.y_min, r.width, r.height)
	}

	mouseDown(x,y) { this.start(y) }
	mouseUp() { this.stop() }

	start(y) {
		if(this.state.taken)return;
		
		if(!this.isStarter&&!this.keyboard.enabled)return;
		if(this.isStarter) {
			this.keyboard.start()
			this.isStarter = false
		}
		
		this.state.taken = true
		const now = this.stopwatch.time
		this.state.selected_t = now+(window.innerHeight-y)/keyHeight
		this.state.selected_swtime = now
		this.startNotes()
	}

	stop() {
		if(!this.state.taken)return;
		this.finish()
		this.playingNotes.forEach(obj=>obj.cancel(0.4))
	}

	startNotes() {
		this.data.notes.forEach(note=>{
			this.playNote(note)
		})
	}

	playNote(note) {
		const synth = this.keyboard.session.synth
		const ctxt_time = synth.context.currentTime
		const s = this.stopwatch.speed

		const t_start = ctxt_time + (note.t0-this.data.t0)/s
		this.playingNotes.push(synth.playNote(note.n, t_start))
	}

}