import {Music} from './music'
import {Keyboard} from './keyboard'
import {Looper} from './custom/utils'

export class PlayingSession {
	constructor(callbacks, synth, canvas, musicParams) {
		this.synth = synth
		this.musicParams = musicParams
		this.callbacks = callbacks

		this.canvas = canvas
		this.canvas.width = window.innerWidth
		this.canvas.height = window.innerHeight
		this.canvas.graphics = this.canvas.getContext("2d")

		this.renderLoop = new Looper(()=>this.draw())
		
		this.music = 0
		this.keyboard = 0

		this.callbacks.setState(0)
		this.state = {
			score: 0,
			errors: 0
		}

		this.load().then((music)=>{
			this.music = music
			this.callbacks.setState(1)
			this.reset()
		})


	}

	async load() {
		const music = (await Promise.all([
			Music.load("files/midiFiles/"+this.musicParams.midiFile,
				this.musicParams.params.converter,
				this.musicParams.params.converterParams),
			this.synth.loading
		]))[0]
		music.speed = this.musicParams.params.speed
		return music
	}

	reset() {
		this.keyboard = new Keyboard(this)
		this.renderLoop.start()
		this.setScore(0)
		this.setErrors(0)
		this.callbacks.setState(2)
	}
	
	dispose() {
		this.renderLoop.stop()
	}

	resume() {
		this.keyboard.resume()
		this.callbacks.setState(2)
	}

	pause() {
		this.keyboard.pause()
		this.callbacks.setState(-1)
	}

	draw() {
		const g = this.canvas.graphics
		g.clearRect(0,0,window.innerWidth,window.innerHeight)
		this.keyboard.draw(g)
	}

	fail() {
		this.addErrors(1)
		this.checkEnd()
	}

	checkEnd() {
		if(this.state.errors>=3){
			this.lost()
		}
	}
	
	lost() {
		this.keyboard.stop()
		this.callbacks.setState(-2)
	}

	addScore(delta) {
		this.setScore(this.state.score+delta)
	}
	addErrors(delta) {
		this.setErrors(this.state.errors+delta)
	}

	setScore(score) {
		this.state.score = score
		this.callbacks.setScore(score)
	}
	setErrors(errors) {
		this.state.errors = errors
		this.callbacks.setErrors(errors)
	}


}
