import {Modal, Button} from 'react-bootstrap'
import ReactDOM from 'react-dom'

export function PauseMenu({quit, retry, show, resume}) {
	
	return ReactDOM.createPortal(<div>
		<Modal show={show}>
			<Modal.Header>
				<Modal.Title>Paused</Modal.Title>
			</Modal.Header>
			<Modal.Body>
				<div className="d-grid gap-2">
					<Button variant="primary" size="lg" onClick={resume}>
						Resume
					</Button>
					<Button variant="secondary" size="lg" onClick={retry}>
						Restart
					</Button>
					<Button variant="secondary" size="lg" onClick={quit}>
						Quit
					</Button>
				</div>
			</Modal.Body>
			<Modal.Footer>
			</Modal.Footer>
		</Modal>
	</div>, document.body)
}