import React from 'react'
import {PlayingSession} from './playingSession'
import {ScoreBoard} from './scoreBoard'
import {PauseMenu} from './pauseMenu'
import {LostMenu} from './lostMenu'

const useSession = (callbacks, synth, canvas, musicParams)=>{
	const [session, setSession] = React.useState(0)
	React.useEffect(()=>{
		if(musicParams) {
			setSession(new PlayingSession(
				callbacks,
				synth,
				canvas.current,
				musicParams
			))
		} else {
			setSession(0)
		}
		return ()=>session?session.dispose():0
	},[musicParams])
	return session
}

export function Playing({quitSession, synth, musicParams, visible}) {
	const canvas = React.useRef(null)
	const [score, setScore] = React.useState()
	const [errors, setErrors] = React.useState()
	const [state, setState] = React.useState(0)
	const session = useSession({setScore, setState, setErrors}, synth, canvas, musicParams)
	
	const quit = ()=>quitSession()
	const pause = ()=>session.pause()
	const resume = ()=>session.resume()
	const retry = ()=>session.reset()
	
	return <div hidden={!visible}>
		<div hidden={state!=0}>Loading...</div>
		<div hidden={state==0}>
			<ScoreBoard pause={pause} errors={errors} score={score} />
			<canvas ref={canvas}></canvas>
		</div>
		<PauseMenu quit={quit} retry={retry} resume={resume} show={session&&state==-1}/>
		<LostMenu quit={quit} retry={retry} show={session&&state==-2}/>
	</div>
}



