export class Stopwatch {
	constructor() {
		this.lastDatetime = 0
		this.lastTime = 0
		this.speed = 1
		this.running = false
	}
	start() {
		if(this.running)return;
		this.lastDatetime = Date.now()
		this.running = true
	}
	stop() {
		if(!this.running)return;
		this.lastTime = this.time
		this.lastDatetime = 0
		this.running = false
	}
	reset() {
		this.lastDatetime = 0
		this.lastTime = 0
		this.running = false
	}
	setSpeed(speed) {
		this.lastTime = this.time
		this.lastDatetime = Date.now()
		this.speed = speed
	}
	setTime(time) {
		this.lastTime = time
		this.lastDatetime = Date.now()
	}
	get time() {
		if(this.running) {
			return this.lastTime+(Date.now()-this.lastDatetime)*(this.speed/1000)
		} else {
			return this.lastTime
		}
	}
}

export class Looper {
	constructor(f) {
		this.function = f
		this.running = false
		
		this.loopFunction = ()=>{
			if(this.running) {
				this.function()
				requestAnimationFrame(this.loopFunction)
			}
		}
	}

	start() {
		if(this.running)return;
		this.running = true
		this.loopFunction()
	}
	stop() {
		this.running = false
	}
}

export function randInt(min, max) {
	return min+Math.floor(Math.random()*(max-min+1))
}

export function max(values) {
	let maxi = -Infinity
	for (let i = 0; i < values.length; i++) {
		if(values[i]>maxi) {
			maxi = values[i]
		}
	}
	return maxi
}

export function argmax(values) {
	let imax = 0
	for (let i = 1; i < values.length; i++) {
		if(values[i]>values[imax]) {
			imax = i
		}
	}
	return imax
}

export function min(values) {
	let mini = Infinity
	for (let i = 0; i < values.length; i++) {
		if(values[i]<mini) {
			mini = values[i]
		}
	}
	return mini
}

export function argmin(values) {
	let imin = 0
	for (let i = 1; i < values.length; i++) {
		if(values[i]<values[imin]) {
			imin = i
		}
	}
	return imin
}