import React from 'react'
import {ListGroup} from 'react-bootstrap'


export function MainTab({newMusic}) {
	const [musics, setMusics] = React.useState(0)
	const loading = !musics
	if(loading) {
		fetch("files/musics.json").then(r=>r.json()).then(obj=>{
			setMusics(obj)
		})
	}

	const onMusicClick = (i)=>newMusic(musics[i])

	return <div>
		{loading&&"Loading..."}
		{!loading&&<List>
			{musics.map((music,i)=>(
				<ListItem
					img="files/Simple_Music.svg"
					key={i} title={music.title}
					onclick={()=>onMusicClick(i)}
					info=""
				/>
			))}
		</List>}
	</div>
}



function List(props) {
	return <div className="list-group w-auto">{props.children}</div>
}
function ListItem({img, title, onclick, info}) {
	return <a
				className="list-group-item list-group-item-action d-flex gap-3 py-3"
				onClick={onclick}>
		<img src={img} alt="twbs" width="32" height="32" className="rounded-circle flex-shrink-0"></img>
		<div className="d-flex gap-2 w-100 justify-content-between">
			<div>
				<h6 className="mb-0">{title}</h6>
				<p className="mb-0 opacity-75"></p>
			</div>
			<small className="opacity-50 text-nowrap">{info}</small>
		</div>
	</a>
}