export class Synth {
	constructor() {
		this.context = 0
		this.buffer = 0
		this.volume = 0.1

		this.loading = 0
	}
	
	init() {
		this.loading=this.load()
		return this.loading
	}

	async load() {
		this.context = new AudioContext()
		const file = "files/41.oga"
		const buffer = await this.context.decodeAudioData(await(await fetch(file)).arrayBuffer())
		this.buffer = buffer
	}
	
	playNote(n, t0=0) {
		return new PlayingNote(this, n, t0)
	}
}

class PlayingNote {
	constructor(synth, n, t0) {
		this.synth = synth
		this.context = this.synth.context
		this.buffer = this.synth.buffer
		this.n = n
		this.t0 = t0
		
		this.source = this.context.createBufferSource()
		this.source.gain = this.context.createGain()
		
		this.source.gain.gain.setValueAtTime(this.synth.volume, 0)
		this.source.gain.gain.setValueAtTime(this.synth.volume, this.t0+5.0)
		this.source.gain.gain.linearRampToValueAtTime(0, this.t0+5.9)

		this.source.buffer = this.buffer
		this.source.connect(this.source.gain)
		this.source.gain.connect(this.context.destination)
		this.source.detune.value = ((this.n-1)%3)*100
		const offset = Math.floor((this.n-1)/3)*16.0
		this.source.start(this.t0, offset, 6.0)
	}

	release(dt=0.4) {
		const now = this.context.currentTime
		this.source.gain.gain.linearRampToValueAtTime(0,now+dt)
	}

	cancel(dt=0) {
		if(!this.isStarted(this.context.currentTime+dt)) {
			this.source.stop()
		}
	}

	isStarted(t) {
		if(t===undefined)t=this.context.currentTime;
		return t>this.t0
	}
}
