

export function ScoreBoard({pause, score, errors}) {
	const styles = {}
	styles.score_div = {
		position: "absolute",
		top: 0, left: "50%",
		transform: "translate(-50%,-50%)",
		textAlign: "center",
		width: "40vw",
		height: "24vw",
		backgroundColor: "#eeffee",
		borderRadius: "10vw"
	}
	
	styles.score_span = {
		position: "absolute",
		top: "0", left: "50%",
		fontSize: "8vw",
		transform: "translate(-50%,0%)"
	}


	styles.error_div = {
		position: "absolute",
		top: 0, right: 0,
		transform: "translate(50%,-50%)",
		width: "40vw",
		height: "20vw",
		backgroundColor: "#eeffee",
		borderRadius: "100%"
	}

	styles.errors_span = {
		position: "absolute",
		top: "0", right: "8vw",
		fontSize: "5vw",
		transform: "translate(50%,0%)"
	}

	styles.pause = {
		position: "absolute",
		top: 0, left: 0,
		transform: "translate(-50%,-50%)",
		width: "40vw",
		height: "40vw",
		backgroundColor: "#eeffee",
		borderRadius: "100%"
	}

	return <div>
		<div style={styles.score_div}></div>
		<div style={styles.error_div}></div>
		<span style={styles.score_span}>{score}</span>
		<span style={styles.errors_span}>{errors}</span>
		<div onClick={pause} style={styles.pause}></div>
	</div>
}