import React from 'react'
import {Synth} from './synth2'
import {Menu} from './menu'
import {Playing} from './playing'

const useSynth = ()=>{
	const [synth, setSynth] = React.useState(new Synth())
	React.useEffect(()=>{
		synth.init()
	},[])
	return synth
}

export function App() {
	const synth = useSynth()
	const [musicParams,setMusicParams] = React.useState(0)

	const quitSession = ()=>setMusicParams(0)

	return <div>
			<Menu
				visible={!musicParams}
				newMusic={setMusicParams} />
			<Playing
				quitSession={quitSession}
				visible={!!musicParams}
				synth={synth}
				musicParams={musicParams} />
		</div>
}



