import {Music} from './music'
import {argmax, min} from './custom/utils'

const converters = {}
export {converters}



converters.c2 = (obj0, {mainTrackId})=>{
	
	var mainNotes = obj0.notes.filter(nt=>nt.trackId==mainTrackId)
	var t0s = mainNotes.map(nt=>nt.t0)
	var intervals = t0s.map((t,i)=>i?t0s[i]-t0s[i-1]:0)
	
	var counts = new Map()
	intervals.forEach((t,i)=>{
		counts.set(t,(counts.get(t)||0)+1)
	})
	var interval = [...counts.keys()][argmax([...counts.values()])]

	var obj = normalizeTime(obj0, {
		nperdiv: 100,
		fraction_unit: obj0.timeDivision/interval
	})

	var data = {keyList: []}
	
	const start_t = min(obj.notes.filter(nt=>nt.trackId==mainTrackId).map(nt=>nt.t0))
	var mainNotes = obj.notes.filter(nt=>nt.t0<start_t||nt.trackId==mainTrackId)
	
	var t = -1
	for(let i = 0; i<mainNotes.length; i++) {
		if(Math.floor(mainNotes[i].t0)>Math.floor(t)) {
			t = Math.floor(mainNotes[i].t0)
			const lastKey = data.keyList[data.keyList.length-1]
			if(lastKey)lastKey.t2 = t;
			data.keyList.push({
				t0:t,t1:0,t2:mainNotes[i].t1,notes:[]
			})
		}
		const lastKey = data.keyList[data.keyList.length-1]
		lastKey.notes.push(mainNotes[i])
	}

	var secondaryNotes = obj.notes.filter(nt=>nt.t0>=start_t&&nt.trackId!=mainTrackId)
	
	if(data.keyList[0].t0>0) {
		data.keyList.unshift({
			t0:0,t1:0,t2:data.keyList[0].t0,notes:[]
		})
	}
	data.keyList.forEach(key=>{
		while(secondaryNotes[0]&&key.t0<=secondaryNotes[0].t0&&secondaryNotes[0].t0<key.t2) {
			key.notes.push(secondaryNotes.shift())
		}
	})



	return data
}



const normalizeTime = (midiNotes, {nperdiv, fraction_unit})=>{
	const result = {timeDivision: midiNotes.timeDivision}
	
	result.notes = midiNotes.notes.map(note=>{
		const scale = fraction_unit/midiNotes.timeDivision
		let t0 = note.t0 * scale
		let t1 = note.t1 * scale
		t0 = Math.round(nperdiv*t0)/nperdiv
		
		const nt = {...note}
		nt.t0 = t0
		nt.t1 = t1
		return nt
	})
	return result
}
